import GlobalConf from '../conf/EnigmaConf'

const Util = {
    getAdaptNumber: (num) => {
        let t = num;
        if (t > GlobalConf.LetterCount) {
            let ti = t % GlobalConf.LetterCount;
            t = ti !== 0 ? ti : GlobalConf.LetterCount;
        }
        return t;
    }

}

export default Util;