import React from "react";
import GlobalConf from '../conf/EnigmaConf'
import {Button, Grid, Message, Select, Typography} from "@arco-design/web-react";

const Option = Select.Option;
const Row = Grid.Row;
const Col = Grid.Col;

const options = [];
for (let i = 1; i <= GlobalConf.LetterCount; i++) {
    options.push(String.fromCharCode(64 + i));
}

const rows = [];
for (let i = 0; i < 10; i++) {
    rows.push(i);
}

class Panel extends React.Component {


    panelLeftWire = []

    panelRightWire = []


    constructor(props, context) {
        super(props, context);
        for (const defaultPanelConfKey in GlobalConf.DefaultPanelConf) {
            this.panelLeftWire.push(defaultPanelConfKey)
            this.panelRightWire.push(GlobalConf.DefaultPanelConf[defaultPanelConfKey])
        }
        this.state = {
            panelLeftWire: this.panelLeftWire,
            panelRightWire: this.panelRightWire,
        };
    }


    panelOnChange = (v) => {
        console.log(v);
        let value = v['value'];
        let splitInfo = value.split("-");
        let rowIndex = parseInt(splitInfo[0]);
        let columnIndex = parseInt(splitInfo[1]);
        let char = splitInfo[2];
        for (let i = 0; i < 10; i++) {
            if (this.panelLeftWire[i] === char || this.panelRightWire[i] === char) {
                Message.warning("already select letter: " + char);
                return false;
            }
        }
        if (columnIndex === 2) {
            this.panelRightWire[rowIndex] = char;
            this.setState(this.panelRightWire)
        }else {
            this.panelLeftWire[rowIndex] = char;
            this.setState(this.panelLeftWire)
        }

        this.props.panelOnChange(v);

    }

    panelReset = () => {
        for (let i = 0; i < 10; i++) {
            this.panelLeftWire[i] = '';
            this.panelRightWire[i] = '';
        }
        this.setState(this.panelLeftWire)
        this.setState(this.panelRightWire)
        this.props.panelReset();
    }

    render() {

        // let _this = this
        return (<>

            <Row style={{textAlign: 'center'}}>
                <Col>
                    <Typography.Title heading={6}>Panel Wire Connect</Typography.Title>

                </Col>
            </Row>

            {rows.map((n, number) => (
                <Row key={"row-key-" + n} justify={'start'}>
                    <Col span={4} offset={1}>
                        <Select
                            placeholder=''
                            style={{width: 100}}
                            labelInValue={true}
                            onChange={this.panelOnChange}
                            value={this.state.panelLeftWire[number]}
                        >
                            {options.map((option, index) => (
                                <Option key={option} value={number + "-1-" + option}>
                                    {option}
                                </Option>
                            ))}
                        </Select>
                    </Col>
                    <Col span={4} offset={7}>
                        <Select
                            placeholder='Please select'
                            style={{width: 100}}
                            labelInValue={true}
                            onChange={this.panelOnChange}
                            value={this.state.panelRightWire[number]}

                        >
                            {options.map((option, index) => (
                                <Option key={option} value={number + "-2-" + option}>
                                    {option}
                                </Option>
                            ))}
                        </Select>

                    </Col>
                </Row>

            ))}
            <Row>
                <Col offset={1}>
                    <Button
                        type='primary'
                        onClick={this.panelReset}
                        >Reset</Button>
                </Col>
            </Row>


        </>);
    }
}


export default Panel;
