import GlobalConf from '../conf/EnigmaConf'

class Reflector {
    reflectConf = new Map();

    _nextCircuit;

    constructor() {
        let reflectorConf = GlobalConf.ReflectorConf;
        for (let i in reflectorConf) {
            let intNumber = parseInt(i, 10);
            this.reflectConf.set(intNumber, reflectorConf[i]);
            this.reflectConf.set(reflectorConf[i], intNumber);

        }
    }


    get nextCircuit() {
        return this._nextCircuit;
    }

    set nextCircuit(value) {
        this._nextCircuit = value;
    }

    emit = (circuitContext) => {
        circuitContext.incrInvokeCount();
        let nextSignal = this.reflectConf.get(circuitContext.signal);
        circuitContext.reverse = true;
        circuitContext.signal = nextSignal;
        return this._nextCircuit !== undefined ? this._nextCircuit.emit(circuitContext) : nextSignal;
    }
}

export default Reflector;