import Gear from './Gear'
import Panel from './Panel'
import Reflector from "./Reflector";
import EnigmaInitConf from "./EnigmaInitConf";
import GlobalConf from '../conf/EnigmaConf'
import CircuitContext from './CircuitContext'

class Enigma {
    headGear;

    tailGear;

    panel;

    reflector;

    constructor() {
        this.initDefault();
    }

    initDefault = () => {
        let enigmaInitConf = new EnigmaInitConf();
        enigmaInitConf.gearPickList = GlobalConf.DefaultGearPick;
        enigmaInitConf.initGapList = GlobalConf.DefaultGearGapPosition;
        enigmaInitConf.initNumberList = GlobalConf.DefaultGearInitNumber;
        let panelWireConnectMap = new Map();

        let panelWireConnect = GlobalConf.DefaultPanelConf;
        for (let i in panelWireConnect) {
            let charCode = i.charCodeAt(0);
            let targetCharCode = panelWireConnect[i].charCodeAt(0);
            panelWireConnectMap.set(charCode - 64, targetCharCode - 64);
            panelWireConnectMap.set(targetCharCode - 64, charCode - 64);
        }
        enigmaInitConf.panelWireConnect = panelWireConnectMap;

        this.reset(enigmaInitConf);

    }

    initGear = (gearList, initPosList, initGapList) => {
        let pre = undefined;
        for (let i = 0; i < 3; i++) {
            let tmpGear = new Gear(gearList[i], initPosList[i], initGapList[i], i);
            if (pre === undefined) {
                this.headGear = tmpGear;
            }
            tmpGear.pre = pre;
            if (pre !== undefined) {
                pre.next = tmpGear;
            }
            pre = tmpGear;
        }
        this.tailGear = pre;
    }

    reset = (enigmaInitConf) => {
        this.initGear(enigmaInitConf.gearPickList, enigmaInitConf.initNumberList, enigmaInitConf.initGapList);
        this.initPanel(enigmaInitConf.panelWireConnect)
        this.initReflector()
        this.initCircuit();
    }

    initPanel = (wireConnect) => {
        this.panel = new Panel(wireConnect);
    }


    initReflector = () => {
        this.reflector = new Reflector();
    }

    initCircuit = () => {
        this.reflector.nextCircuit = this.tailGear;
        this.tailGear.reflector = this.reflector;

        this.headGear.panel = this.panel;
        this.panel.nextCircuit = this.headGear;


    }

    process = (single) => {
        let circuitContext = new CircuitContext(single);
        this.headGear.tick();
        return this.panel.emit(circuitContext);
    }
}

export default Enigma;