class Panel {

    wirePairConf = new Map();

    _nextCircuit;

    constructor(wirePairConf) {
        this.wirePairConf = wirePairConf;
    }


    get nextCircuit() {
        return this._nextCircuit;
    }

    set nextCircuit(value) {
        this._nextCircuit = value;
    }

    emit = (circuitContext) => {
        console.log("panel context:" + circuitContext.signal);
        circuitContext.incrInvokeCount();
        console.log(this.wirePairConf);
        let nextSignal = this.wirePairConf.get(circuitContext.signal);
        if (nextSignal === undefined) {
            nextSignal = circuitContext.signal;
        }
        console.log("panel next single " + nextSignal);
        circuitContext.signal = nextSignal;
        if (!circuitContext.reverse) {
            return this._nextCircuit !== undefined ? this._nextCircuit.emit(circuitContext) : nextSignal;
        }
        return nextSignal;


    }


}

export default Panel;