import React from "react";
import GearList from "./GearList";
import {Button, Grid, Layout, Typography, Input, Message, Switch, Card} from '@arco-design/web-react';
import _Enigma from '../device/Enigma'
import GlobalConf from '../conf/EnigmaConf'
import EnigmaInitConf from "../device/EnigmaInitConf";
import Panel from "./Panel";

const Row = Grid.Row;
const Col = Grid.Col;
const Sider = Layout.Sider;
const Header = Layout.Header;
const Footer = Layout.Footer;
const Content = Layout.Content;
const TextArea = Input.TextArea;


class Enigma extends React.Component {
    gearPick = GlobalConf.DefaultGearPick.concat();

    gearInitNumber = GlobalConf.DefaultGearInitNumber.concat();

    gearGapNumber = GlobalConf.DefaultGearGapPosition.concat();

    keepFormat = false;

    panelLeftWire = []

    panelRightWire = []

    inputValue;

    DeviceEnigma;

    constructor(props, context) {
        super(props, context);
        this.DeviceEnigma = new _Enigma();
        for (const defaultPanelConfKey in GlobalConf.DefaultPanelConf) {
            this.panelLeftWire.push(defaultPanelConfKey.charCodeAt(0) - 64)
            this.panelRightWire.push(GlobalConf.DefaultPanelConf[defaultPanelConfKey].charCodeAt(0) - 64)
        }
        this.state = {showValue: ""};


    }

    panelReset = () => {
        for (let i = 0; i < 10; i++) {
            this.panelRightWire[i] = -1;
            this.panelLeftWire[i] = -1;
        }

    }

    reset = (e) => {
        let enigmaInitConf = new EnigmaInitConf();
        enigmaInitConf.gearPickList = this.gearPick;
        enigmaInitConf.initNumberList = this.gearInitNumber;
        enigmaInitConf.initGapList = this.gearGapNumber;
        let panelConfMap = new Map();
        for (let i = 0; i < 10; i++) {
            if (this.panelLeftWire[i] === -1 || this.panelRightWire[i] === -1) {
                Message.error("please fix panel wire connect!")
                return;
            }
        }
        for (let i = 0; i < 10; i++) {
            panelConfMap.set(this.panelLeftWire[i], this.panelRightWire[i]);
            panelConfMap.set(this.panelRightWire[i], this.panelLeftWire[i]);
        }
        enigmaInitConf.panelWireConnect = panelConfMap;
        console.log(enigmaInitConf);
        this.DeviceEnigma.reset(enigmaInitConf);
        Message.info("Reset success!");
    }


    componentDidMount() {
    }

    keepFormatOnChange = (v) => {
        console.log(v);
        this.keepFormat = v;
    }

    gearPickOnChange = (v) => {
        this.gearPick = v;
    }

    inputValueOnChange = (v) => {
        this.inputValue = v;
    }

    onTrans = () => {
        let inputValue1 = this.inputValue;
        let resultList = [];
        for (let i = 0; i < inputValue1.length; i++) {
            let cc = inputValue1.charAt(i).charCodeAt(0);
            if (cc >= 65 && cc <= 90) {
                let single = cc - 64;
                let t = this.DeviceEnigma.process(single);
                resultList.push(String.fromCharCode(64 + t));

            } else if (cc >= 97 && cc <= 122) {
                let single = cc - 96;
                let t = this.DeviceEnigma.process(single);
                if (this.keepFormat) {
                    resultList.push(String.fromCharCode(96 + t));
                } else {
                    resultList.push(String.fromCharCode(64 + t));
                }
            } else if (this.keepFormat) {
                console.log("keep format:" + inputValue1.charAt(i))
                resultList.push(String(inputValue1.charAt(i)));
            }

            this.setState({showValue: resultList.join("")});

            console.log("result: " + resultList);

        }
    }

    gearNumberOnChange = (v) => {
        console.log(v);
        let value = v['value'];
        let splitInfo = value.split("-");
        let index = parseInt(splitInfo[0]);
        this.gearInitNumber[index] = parseInt(splitInfo[1], 10);
        console.log(this.gearInitNumber);

    }

    gearGapNumberOnChange = (v) => {
        console.log(v);
        let value = v['value'];
        let splitInfo = value.split("-");
        let index = parseInt(splitInfo[0]);
        this.gearGapNumber[index] = parseInt(splitInfo[1], 10);

        console.log(this.gearGapNumber);
    }

    panelOnChange = (v) => {
        // {value: '0-1-D', label: 'D'}
        let value = v['value'];
        let splitInfo = value.split("-");
        let rowIndex = parseInt(splitInfo[0]);
        let columnIndex = parseInt(splitInfo[1]);
        let charCode = splitInfo[2].charCodeAt(0) - 64;
        if (columnIndex === 2) {
            this.panelRightWire[rowIndex] = charCode;
        } else {
            this.panelLeftWire[rowIndex] = charCode;
        }
        console.log(this.panelLeftWire);
        console.log(this.panelRightWire);
    }

    render() {

        return (<div>
            <Layout style={{height: '1200px'}}>
                <Header style={{textAlign: 'center'}}>
                    <Typography.Title heading={1}>Enigma-Box</Typography.Title>
                </Header>
                <Layout>
                    <Content>
                        <Row style={{ width: GlobalConf.Width }} align={'center'}>
                            <Col offset={GlobalConf.Offset} span={1.5}>
                                <Card style={{width: 360}}
                                      title='For mmd && yyd first movie'
                                >
                                    2015-08-22
                                    Imitation Game
                                </Card>
                            </Col>
                        </Row>


                        <GearList
                            gearPickOnChange={this.gearPickOnChange}
                            gearNumberOnChange={this.gearNumberOnChange}
                            gearGapNumberOnChange={this.gearGapNumberOnChange}

                        />

                        <Row style={{width: GlobalConf.Width}} align={'center'}>
                            <Col offset={GlobalConf.Offset}>
                                <Button
                                    type='primary'
                                    onClick={this.reset}>Reset</Button>
                            </Col>
                        </Row>

                        <br/>

                        <Row style={{width: GlobalConf.Width}} align={'center'}>

                            <Col offset={GlobalConf.Offset} span={6}>

                                <TextArea placeholder='Please enter ...'
                                          style={{minHeight: 200, width: 350, borderColor: "purple", resize: "none"}}
                                          onChange={this.inputValueOnChange}
                                          autoSize={false}/>

                            </Col>

                            <Col span={2}>
                                <Button
                                    type='primary'
                                    onClick={this.onTrans}
                                >Trans</Button>
                            </Col>

                            <Col span={6}>
                                <TextArea style={{minHeight: 200, width: 350, borderColor: "purple", resize: "none"}}
                                          value={this.state.showValue}
                                          readOnly={true}/>
                                Keep-Format:<Switch onChange={this.keepFormatOnChange}/>
                            </Col>
                            <Col span={1} styel={{marginLeft: "2px", marginTop: "100px"}}>
                            </Col>
                        </Row>


                    </Content>
                    <Sider style={{width: "300px"}}>
                        <Panel
                            panelOnChange={this.panelOnChange}
                            panelReset={this.panelReset}
                        />
                    </Sider>

                </Layout>
                <Footer>

                </Footer>
            </Layout>


        </div>);
    }
}


export default Enigma;
