import {Message, Select, Typography} from '@arco-design/web-react';
import React from "react";
import GlobalConf from '../conf/EnigmaConf'
import {Grid} from '@arco-design/web-react';
import GearInitNumber from './GearInitNumber';
import GearInitGapNumber from './GearInitGapNumber';

const Row = Grid.Row;
const Col = Grid.Col;


const Option = Select.Option;
const options = [1, 2, 3, 4, 5];

class GearList extends React.Component {


    constructor(props, context) {
        super(props, context);
        this.state = {
            value: GlobalConf.DefaultGearPick
        }
    }

    componentDidMount() {
    }

    onChange = (v) => {
        if (v.length > 3) {
            Message.error('gear can not exceed three!');
        } else {
            this.setState({value: v})
            this.props.gearPickOnChange(v);
        }

    }

    render() {

        return (
            <div style={{ width: GlobalConf.Width }}>

                <Row style={{ width: GlobalConf.Width }} align={'center'}>
                    <Col offset={GlobalConf.Offset} span={1.5}>
                        <Typography.Title heading={6}>Pick-Gear:</Typography.Title>

                    </Col>
                    <Col span={2} style={{marginLeft:"12px"}}>
                        <Select
                            placeholder='Please select'
                            style={{width: 300}}
                            mode='multiple'
                            dragToSort
                            value={this.state.value}
                            onChange={this.onChange}
                        >
                            {options.map((option, index) => (<Option key={option} value={option}>
                                {option}
                            </Option>))}
                        </Select>
                    </Col>
                </Row>


                <GearInitNumber
                    key = {"GearInitNumber"}
                    gearNumberOnChange={this.props.gearNumberOnChange}
                />


                <GearInitGapNumber
                    gearGapNumberOnChange={this.props.gearGapNumberOnChange}
                />


            </div>);
    }
}


export default GearList;
