import React from "react";
import {Select, Grid, Typography,} from "@arco-design/web-react";
import GlobalConf from "../conf/EnigmaConf";
import * as util from "util";

const Option = Select.Option;
const Row = Grid.Row;
const Col = Grid.Col;
const numberOptions = [];
for (let i = 1; i <= GlobalConf.LetterCount; i++) {
    numberOptions.push(i)
}
const title = "Gear%d-Gap:";
const marginLeft = {marginLeft: "5px"};


const itermCount = [0, 1, 2];

class GearInitGapNumber extends React.Component {


    render() {

        return (
            <>
                <Row style={{width: GlobalConf.Width}} align={'center'}>

                    {
                        itermCount.map((n, i) => (
                            <Item
                                key={"item-key-" + n}
                                order={n}
                                gearGapNumberOnChange={this.props.gearGapNumberOnChange}
                            />
                        ))
                    }
                </Row>

            </>);
    }
}


class Item extends React.Component {


    render() {
        return (
            <>
                <Col offset={this.props.order === 0 && GlobalConf.Offset} span={1.5}>
                    <Typography.Title heading={6}>{util.format(title, this.props.order + 1)}</Typography.Title>
                </Col>

                <Col span={2} style={marginLeft}>
                    <Select
                        placeholder='Please select'
                        style={{width: 100}}
                        labelInValue={true}
                        defaultValue={GlobalConf.DefaultGearGapPosition[this.props.order]}
                        onChange={this.props.gearGapNumberOnChange}
                    >
                        {numberOptions.map((option, index) => (
                            <Option key={option} value={this.props.order + "-" + option}>
                                {option}
                            </Option>))}
                    </Select>

                </Col>
            </>

        );
    }
}

export default GearInitGapNumber;