class EnigmaInitConf {
    _gearPickList;
    _initGapList;
    _initNumberList;
    _panelWireConnect;

    get panelWireConnect() {
        return this._panelWireConnect;
    }

    set panelWireConnect(value) {
        this._panelWireConnect = value;
    }


    get gearPickList() {
        return this._gearPickList;
    }

    set gearPickList(value) {
        this._gearPickList = value;
    }

    get initGapList() {
        return this._initGapList;
    }

    set initGapList(value) {
        this._initGapList = value;
    }

    get initNumberList() {
        return this._initNumberList;
    }

    set initNumberList(value) {
        this._initNumberList = value;
    }
}

export default EnigmaInitConf;