class CircuitContext {
    _signal;

    _reverse = false;

    invokeCount = 0;

    constructor(signal) {
        this._signal = signal;
    }

    incrInvokeCount = () => {
        this.invokeCount++;
    }


    get signal() {
        return this._signal;
    }

    set signal(value) {
        this._signal = value;
    }

    get reverse() {
        return this._reverse;
    }

    set reverse(value) {
        this._reverse = value;
    }
}

export default CircuitContext;