import GlobalConf from '../conf/EnigmaConf'
import Util from '../util/Util'

class Gear {

    _pre;

    _next;

    _reflector;

    _panel;

    currentPosition;

    rotateCount = 0;

    gapPosition;

    initPosition;

    gearNumber;

    wireConnect = new Map();
    reverseWireConnect = new Map();

    positionWireMap = new Map();
    reversePositionWireMap = new Map();


    constructor(gearPick, initPosition, gapPosition, gearNumber) {
        let gearConnectConf = GlobalConf.GearConf[gearPick];

        this.gearNumber = gearNumber;

        for (const confObjKey in gearConnectConf) {
            this.wireConnect.set(parseInt(confObjKey, 10), gearConnectConf[confObjKey]);
            this.reverseWireConnect.set(gearConnectConf[confObjKey], parseInt(confObjKey, 10));
        }


        this.currentPosition = initPosition;
        this.gapPosition = gapPosition;
        this.initPosition = initPosition;

        this.init();
    }


    get pre() {
        return this._pre;
    }

    set pre(value) {
        this._pre = value;
    }

    get next() {
        return this._next;
    }

    set next(value) {
        this._next = value;
    }

    get reflector() {
        return this._reflector;
    }

    set reflector(value) {
        this._reflector = value;
    }

    get panel() {
        return this._panel;
    }

    set panel(value) {
        this._panel = value;
    }

    init = () => {

        let currentPost = this.currentPosition;
        for (let i = 0; i < GlobalConf.LetterCount; i++) {
            currentPost = Util.getAdaptNumber(currentPost);
            let amendPos = i + 1;
            this.positionWireMap.set(amendPos, currentPost);

            let mappingPos = this.wireConnect.get(currentPost);
            let reverseRealPos = 1 + GlobalConf.LetterCount + (mappingPos - this.initPosition);
            reverseRealPos = Util.getAdaptNumber(reverseRealPos);
            this.reversePositionWireMap.set(reverseRealPos, mappingPos);
            currentPost++;

        }

    }


    tick = () => {
        this.rotateCount++;
        if (this.currentPosition === this.gapPosition && this._next !== undefined) {
            this._next.tick();
        }
        this.currentPosition++;
        this.currentPosition = Util.getAdaptNumber(this.currentPosition);
    }

    emit = (circuitContext) => {
        circuitContext.incrInvokeCount();
        let reverse = circuitContext.reverse;
        let signal = circuitContext.signal;
        let pwm = this.positionWireMap;
        let wc = this.wireConnect;
        let nextCircuit = this._next;
        if (reverse) {
            nextCircuit = this._pre;
            if (this._panel !== undefined) {
                nextCircuit = this._panel;
            }
            pwm = this.reversePositionWireMap;
            wc = this.reverseWireConnect;
        } else if (this._reflector !== undefined) {
            nextCircuit = this._reflector;
        }


        let correctPos = Util.getAdaptNumber(signal + (this.rotateCount % GlobalConf.LetterCount));
        let posGearNumber = pwm.get(correctPos);
        let mappingGearNumber = wc.get(posGearNumber);

        let outPosition = Util.getAdaptNumber(1 + GlobalConf.LetterCount + (mappingGearNumber - this.currentPosition));
        circuitContext.signal = outPosition;
        console.log("number:" + this.gearNumber + " reverse:" + reverse + " signal: " + signal + " correctPos:" + correctPos + " rotateCount:" + this.rotateCount + " outPosition:" + outPosition);

        return nextCircuit !== undefined ? nextCircuit.emit(circuitContext) : circuitContext.signal;

    }

}


export default Gear;